body {
  background-image: url(https://html5book.ru/wp-content/uploads/2016/04/background68.png);
  margin: 0;
  padding: 0;
}
li {
  list-style-type: none;
  text-align: center;
  border: 1px solid;
  width: 19%;
}
a {
  text-decoration: none;
  display: flex;
  justify-content: space-around;
  font-weight: 600;
}
ul {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  margin: auto;
}
.green {
  color: green;
}
form {
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 35%;
  margin: 20px auto;
  border: 1px solid darkgoldenrod;
  background-color: antiquewhite;
  justify-content: space-between;
  position: relative;
  border-radius: 10px;
  box-shadow: 0px 5px 10px 2px rgb(34 60 80 / 20%);
}
.non-active {
  display: none;
}
form input {
  width: 200px;
  margin: 0 auto;
}
.active {
  display: block;
}
.delete {
  position: absolute;
  right: 5%;
  top: 5%;
  border-radius: 5px;
  border: none;
  width: 50px;
  height: 30px;
}
h1 {
  text-align: center;
}
.Home_button {
  background: antiquewhite;
  color: black;
  font-size: 20px;
  margin-top: 2em;
  border-radius: 2em;
  border: 1px solid bisque;
  display: flex;
  margin: 50px auto;
}
button:hover {
  cursor: pointer;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}
button:active {
  box-shadow: inset 2px 2px 5px rgba(154, 147, 140, 0.5),
    1px 1px 5px rgba(255, 255, 255, 1);
}
.rating {
  position: relative;
  font-size: 40px;
  display: inline-block;
  width: 200px;
  margin: 0 auto;
}
.rating::before {
  content: '★★★★★';
  display: block;
}
.rating_items {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row-reverse;
  overflow: hidden;
}
.rating_item {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
  top: 0;
  left: 0;
}
.rating_label {
  flex: 0 0 20%;
  height: 100%;
  cursor:  pointer;
  color: gray;
}
.rating_label::before {
  content: '★';
  display: block;
  transition: color 0.3s ease 0s ;
}
.rating_label:hover,
.rating_label:hover ~ .rating_label,
.rating_label:checked ~ .rating_label:hover{
  color: gold;
}
.rating_item:checked,
.rating_item:checked ~ .rating_label {
  color: gold;
}
form input:last-child {
  border-radius: 5px;
  border: none;
  width: 60px;
  height: 30px;
  margin: 0 auto 20px;
}
form input:last-child:hover {
  cursor: pointer;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}
form input:last-child:active {
  box-shadow: inset 2px 2px 5px rgba(154, 147, 140, 0.5),
    1px 1px 5px rgba(255, 255, 255, 1);
}
form p {
  text-align: center;
}
.title {
  margin-left: 20px;
  font-size: 16px;
  font-weight: 600;
}
